import { QRCodeCanvas } from 'qrcode.react';
import { useState } from 'react';

type FormData = {
  efmsNo: string;
  species: string;
  conveyance: string;
  implements: string;
  pieces: string;
  place: string;
  dateApprehended: string;
  caseNo: string;
  dateFiled: string;
  suspects: string;
  nature: string;
  officer: string;
  custodian: string;
  depository: string;
  RCO: string;
  RCO_No: string;
};

const initialForm: FormData = {
  efmsNo: '',
  species: '',
  conveyance: '',
  implements: '',
  pieces: '',
  place: '',
  dateApprehended: '',
  caseNo: '',
  dateFiled: '',
  suspects: '',
  nature: '',
  officer: '',
  custodian: '',
  depository: '',
  RCO: '',
  RCO_No: '',
};

export const Form = () => {
  const [form, setForm] = useState<FormData>(initialForm);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ): void => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const downloadQRCode = (): void => {
    const canvas = document.getElementById('qr-code') as HTMLCanvasElement;
    const pngUrl = canvas
      .toDataURL('image/png')
      .replace('image/png', 'image/octet-stream');
    const downloadLink = document.createElement('a');
    downloadLink.href = pngUrl;
    downloadLink.download = `qr-code-${form.efmsNo || 'data'}.png`;
    downloadLink.click();
  };

  const formatKey = (key: string): string =>
    key.replace(/([A-Z])/g, ' $1').replace(/^./, (str) => str.toUpperCase());

  const formatData = (): string =>
    Object.entries(form)
      .map(([key, value]) => `${formatKey(key)}: ${value}`)
      .join('\n');

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <div className="max-w-4xl mx-auto bg-white p-6 rounded shadow ">
        <div className="bg-gray-900 p-4 rounded">
          <h1 className="text-2xl font-bold text-center bg-gradient-to-r from-[#0000FF] via-[#FCFCFF] to-[#50FF50] bg-clip-text text-transparent">
            Enforcement QR Code Generator
          </h1>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
          {Object.entries(form).map(([key, value]) => (
            <div key={key}>
              <label className="block text-sm font-medium mb-1 capitalize">
                {formatKey(key)}
              </label>
              <input
                type={key.toLowerCase().includes('date') ? 'date' : 'text'}
                name={key}
                value={value}
                onChange={handleChange}
                className="w-full border border-gray-300 p-2 rounded"
              />
            </div>
          ))}
        </div>

        <div className="mt-6 flex flex-col items-center">
          <QRCodeCanvas
            id="qr-code"
            value={formatData()}
            size={200}
            level="H"
            includeMargin={true}
          />
          <button
            onClick={downloadQRCode}
            className="mt-4 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
          >
            Download QR Code
          </button>
        </div>
      </div>
    </div>
  );
};
